#section-myworks{
    background: url(./resources/blob-bg.svg);
    background-size: cover;
}


#Myworks-main-body {
    position: absolute;
    top: 0;
    /* left: 10%; */
    /* right: 10%; */
    width: 100%;
    height: 100%;
    background: #c9a36b;
    overflow: hidden;
    /* background-image: url(https://cdn4.vectorstock.com/i/1000x1000/86/33/scenery-garden-with-sunflower-vector-20918633.jpg); */
}

.blur {
    position: absolute;
    width: 10%;
    height: 100%;
    filter: none;
    background-color: inherit;
    /* border-radius: inherit; */
    z-index: 1;
    background: #04152582;
    filter: blur(10px) drop-shadow(0px 0px 20px white);
}


#Myworks-main-div {
    background: grey;
    /* height: 80vh; */
    width: 80vw;
    aspect-ratio: 1/1;
    max-height: 80vh;
    transform: translate(-50%,-50%);
    position: absolute;
    left: 50%;
    top: 50%;
    border-radius: 0.5rem;
    overflow: hidden;
    filter: drop-shadow(6px 6px 2px black);
}


.arrow {
    position: absolute;
    width: 10%;
    height: 100%;
    /* filter: none; */
    /* background-color: inherit; */
    /* border-radius: inherit; */
    z-index: 1;
    /* background: #04152582; */
    /* filter: blur(10px) drop-shadow(0px 0px 20px white); */
    background-repeat: no-repeat;
    background-image: url(./resources/down-arrow.svg);
    background-position: center;
}